      editLesson: false,
      showEditLessonDetail: false,
      showAddNewLesson: false,
      isButtonLoadingComplete: false,
      isLoadingLessons: true,
      isLoadingPayouts: true,
    };
  },
  mounted() {
    bus.$on('addLesson',data => {
      this.preview(data)
      this.showAddNewLesson = true
    })
  },
  methods: {
    async updateTeacher() {
      let self = this;
      console.log("更新教师信息: ", self.previewData);
      console.log("name: ", self.fullName);
      console.log("email: ", self.email);
      console.log("phone: ", self.phone);
      console.log("website: ", self.url);

      await collections.user
        .doc(self.previewData.userId)
        .update({
          name: self.fullName,
          email: self.email,
          phone: self.phone,
          website: self.url,
        })
        .then(() => {
          self.showEditTeacher = false;
          self.isButtonLoadingComplete = true;
          (self.previewData.name = self.fullName),
            (self.previewData.email = self.email),
            (self.previewData.phone = self.phone),
            (self.previewData.website = self.url);
          self.$notify({
            message: "Update Success!",
            type: "success",
          });
        })
        .catch((error) => {
          console.log(error);
          self.showEditTeacher = false;
          self.$notify({
            message: "Update failed, try it again!",
            type: "error",
          });
        });

      self.isButtonLoadingComplete = false;
    },
    async showLessonDetail(data) {
      let self = this;
      if (!self.editLesson) {
        self.showEditLessonDetail = true;
        self.lessonData = data;
        let student = await query.getUserInfoByUserId(data.studentId);
        if (!self.$tools.isNull(student)) {
          self.studedntBasicData = {
            name: student.name,
            email: student.email,
            phone: student.phone,
            userId: student.userId,
            avatar: commons.userAvatarPath(student.userId)
          };
        } else {
          self.studedntBasicData = {
            name: "",
            email: "",
            phone: "",
            avatar: ""
          };
        }
      }
    },
    deleteLesson(item) {
      let self = this
      let deleteLessonBasedOnConfig = self.$firebase
        .functions()
        .httpsCallable("deleteLessonsAfterSpecificTimeBasedOnConfigId");
      console.log(item)
      // deleteLessonBasedOnConfig({
      //   time: parseInt(new Date().getTime() / 1000),
      //   configId: self.lessonData.originData.lessonScheduleConfigId,
      //   isUpdate: true,
      //   studentId: self.lessonData.studentId,
      //   teacherId: self.lessonData.teacherId,
      //   newScheduleData:
      // })
      //   .then(async (result) => {
      //     console.log(result.data.data);
      //     self.$notify({
      //       message: "Update Success!",
      //       type: "success",
      //     });

      //     self.$emit("update");
      //     self.modalClose();
      //   })
      //   .catch((err) => {
      //     console.log(err);
      //     self.$notify({
      //       message: "Update failed, try it again!",
      //       type: "error",
      //     });
      //     self.isButtonLoadingComplete = true;
      //   });
    },
    toggleEditLesson() {
      let self = this;
      self.editLesson = !self.editLesson;
      $("#addNewLesson").slideToggle();
    },
    validationEmail() {
      let self = this;
      if (self.$tools.isNull(self.email)) {
        self.isInvalid = false;
      } else {
        self.isInvalid = !self.$tools.isEmail(self.email);
      }
    },
    async preview(data) {
      console.log("点击教师item: ", data);
      let self = this;
      self.showPreview = true;
      self.previewData = data;
      self.lessonData.teacherId = data.userId
      console.log("lessonData: ", self.lessonData)
      self.isLoadingLessons = true;
      self.initUserBasic();
      await self.initLessons(data.userId)
      setTimeout(() => {
        self.isLoadingLessons = false;
      }, 1000);
    },
    async initLessons(teacherId) {
      this.lessons = await teacherService.listNextLessonsByTeacher(teacherId);
      console.log('init lessons: ', this.lessons)
      bus.$emit("refreshTeacherLessonCount", {
        teacherId: teacherId,
        count: this.lessons.length
      })
    },
    initUserBasic() {
      let self = this;
      self.fullName = self.previewData.name;
      self.email = self.previewData.email;
      self.phone = self.previewData.phone;
      self.url = self.previewData.website;
    },
    onActive() {
      let self = this;
      self.previewData.active = true;
      bus.$emit("onActive", self.previewData);
    },
    onDeleteArchive() {
      let self = this;
      self.deleteActive = false;
      self.showDelete = true;
    },
    onDelete() {
      let self = this;
      self.deleteActive = true;
      self.showDelete = true;
    },
    onArchive() {
      let self = this;
      self.previewData.active = false;
      bus.$emit("onArchive", self.previewData);
    },
    confirmDelete() {
      let self = this;
      bus.$emit("confirmDelete", self.previewData);
      self.showDelete = false;
      self.showPreview = false;
    },
